.Selector{
    padding: 10px;
    width: 80%;
  margin: auto;
    background-color: rgba(144, 27, 91, 0.277);
    height: 20vh;
    display: flex;
    justify-content: space-around;
position: relative;
z-index: 100;
box-shadow: 10px 20px 40px rgba(102, 51, 153, 0.278);
border-radius: 10px;;
top: 50px;
}
.Info{
    margin-top: 3rem;
    display: flex;
    font-size: 3rem;
    justify-content: center;
    color: rgb(148, 6, 13);
}
.Info-hor{
    flex-wrap: wrap;
    display: flex;

    justify-content: space-around;

}
.Info-Box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 65%;
    border-radius: 40px ;
    height: 60vh;
    background: hsla(192, 96%, 50%, 1);

    background: linear-gradient(90deg, hsla(192, 96%, 50%, 1) 0%, hsla(65, 92%, 54%, 1) 100%);
    
    background: -moz-linear-gradient(90deg, hsla(192, 96%, 50%, 1) 0%, hsla(65, 92%, 54%, 1) 100%);
    
    background: -webkit-linear-gradient(90deg, hsla(192, 96%, 50%, 1) 0%, hsla(65, 92%, 54%, 1) 100%);
    
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr="#05C9F9", endColorstr="#E5F61B", GradientType=1 );
}
